.product-list {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  padding-top: 30px;
  width: 81.8%;

  list-style: none;
}

.product-list__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  border: 1px solid $color-border;

  &:hover .product-list__info {
    box-shadow: 0 6px 10px 0 rgba($black, 0.1);
  }

  &:focus .product-list__info {
    box-shadow: 0 3px 5px 0 rgba($black, 0.1);
  }
}

.product-list__info {
  display: flex;
  flex-direction: column;
  padding: 22px 27px 27px 28px;
}

.product-list__description {
  margin-bottom: 19px;
}

.product-list__link {
  color: $black;

  &:hover .product-list__title {
    opacity: 0.6;
  }

  &:focus {
    outline: none;

    .product-list__title {
      outline: none;
      opacity: 0.3;
    }
  }
}

.product-list__title {
  margin: 0;

  font-size: 17px;
  font-weight: $font-weight-bold;
}

.product-list__characteristics {
  margin: 0;
  margin-top: 4px;

  font-size: 13px;
  line-height: 30px;
  color: $color-main;
}

.product-list__purchase {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  padding-top: 21px;

  border-top: 1px solid $color-border;
}

.product-list__price {
  color: $black;
  font-weight: $font-weight-bold;
}

.product-list__buy {
  width: 20px;
  height: 19px;
  margin-left: auto;
  padding: 0;

  background: none;
  border: none;

  cursor: pointer;

  &:hover {
    .product-list__buy-icon {
      fill: $color-secondary;
      opacity: 1;
    }
  }

  &:focus {
    outline: none;

    .product-list__buy-icon {
      fill: $color-secondary;
      opacity: 0.3;
    }
  }
}

.product-list__buy-icon {
  opacity: 0.2;
}

@media (min-width: $tablet-width) {
  .product-list {
    width: 86.8%;
    margin: 0 auto 0;
    padding: 65px 0 0;
  }

  .product-list__item {
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    margin-bottom: 53px;
  }

  .product-list__info {
    width: calc(100% - 334px);
    padding: 26px 31px 17px 25px;
  }

  .product-list__image {
    display: block;
    width: 334px;
    height: 334px;
  }

  .product-list__title {
    font-size: 20px;
  }

  .product-list__description {
    width: auto;
    margin: 0;
  }

  .product-list__purchase {
    width: auto;
    margin-top: auto;
    padding-top: 22px;
    padding-bottom: 11px;
    padding-left: 5px;
  }

  .product-list__characteristics {
    margin-top: 5px;
    font-size: 15px;
  }

  .product-list__price {
    font-size: 17px;
  }

  .product-list__buy {
    margin-right: 0;
  }
}

@media (min-width: $desktop-width) {
  .product-list {
    width: 91.5%;
    padding: 112px 0 0;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .product-list__item {
    flex-direction: column;
    width: 316px;
    padding: 0;
  }

  .product-list__info {
    width: 100%;
    min-height: 197px;
    padding-top: 27px;
    padding-bottom: 14px;
    padding-left: 26px;
  }

  .product-list__image {
    width: 316px;
    height: 462px;
  }

  .product-list__purchase {
    width: 256px;
    padding-top: 24px;
    padding-left: 0;
  }

  .product-list__description {
    margin: 0;
  }
}
