.title {
  margin: 0;
  padding-top: 34px;
  font-size: 25px;
  line-height: 30px;
  font-weight: $font-weight-bold;
  color: $black;
  text-align: left;
}

@media (min-width: $tablet-width) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}

@media (min-width: $desktop-width) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
